/*
 * Define the regular expressions that will be used
*/
const regexSet = {
  ruleRegex: /^(.+?)\[(.+)\]$/,
  numericRegex: /^[0-9]+$/,
  integerRegex: /^\-?[0-9]+$/,
  decimalRegex: /^\-?[0-9]*\.?[0-9]+$/,
  emailRegex: /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/,
  alphaRegex: /^[a-z]+$/i,
  alphaNumericRegex: /^[a-z0-9]+$/i,
  alphaDashRegex: /^[a-z0-9_\-]+$/i,
  naturalRegex: /^[0-9]+$/i,
  naturalNoZeroRegex: /^[1-9][0-9]*$/i,
  ipRegex: /^((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){3}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})$/i,
  base64Regex: /[^a-zA-Z0-9\/\+=]/i,
  numericDashRegex: /^[\d\-\s]+$/,
  urlRegex: /^((http|https):\/\/(\w+:{0,1}\w*@)?(\S+)|)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?$/,
  dateRegex: /\d{4}-\d{1,2}-\d{1,2}/,
};


const isMinLength = (value, minLength = 0) => (value.length >= minLength);
const isMaxLength = (value, maxLength = 0) => (value.length <= maxLength);
const isEmail = value => value.match(regexSet.emailRegex);
const isUrl = value => value.match(regexSet.urlRegex);
const isAlpha = value => value.match(regexSet.alphaRegex);
const isAlphaDash = value => value.match(regexSet.alphaDashRegex);
const isAlphaNum = value => value.match(regexSet.alphaNumericRegex);
const isInteger = value => value.match(regexSet.integerRegex);
const isDecimal = value => value.match(regexSet.decimalRegex);
const isNumeric = value => value.match(regexSet.numericRegex);
const isBase64 = value => value.match(regexSet.base64Regex);
const isArray = value => Array.isArray(value);
const isBoolean = value => (typeof value === 'boolean');
const isString = value => (typeof value === 'string');
const isDate = value => (value instanceof Date); // || value instanceof moment);
const isNullable = value => !value;
const isRegex = (value, regex) => value.match(regex);
const isSize = (value, size) => value.length === size;

const isRequired = value => (
  value !== null ||
  value !== undefined ||
  value !== 0 ||
  value !== '' ||
  value !== {} ||
  value !== []
);

